<template>
  <div class="user-list" v-if="users.length > 0">
    <UserItem
      v-for="user in users"
      :user="user"
      :key="user.email"
      @toggleSuitable="toggleSuitable"
      :isSuitable="suitables.includes(user.email)"
    />
    <div class="loader" ref="loader"></div>
  </div>
  <div class="not-found" v-else>Not found :(</div>
</template>

<script>
import UserItem from "@/components/user/UserItem.vue";

export default {
  name: "UserList",
  components: {
    UserItem,
  },
  data() {
    return {
      suitables: [],
    };
  },
  props: {
    users: Array,
  },
  mounted() {
    this.initObserver();
  },
  methods: {
    initObserver() {
      const intersectionObserver = new IntersectionObserver(
        () => {
          this.$emit("loadNextPage");
        },
        {
          root: null,
          rootMargin: "0px",
          threshold: [1],
        }
      );

      intersectionObserver.observe(this.$refs.loader);
    },
    toggleSuitable(email) {
      if (this.suitables.includes(email)) {
        this.suitables.splice(this.suitables.indexOf(email), 1);
      } else {
        this.suitables.push(email);
      }
    },
  },
};
</script>

<style scoped>
.user-list {
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding: 20px 0;
}

.not-found {
  padding: 10px;
}
</style>
