<template>
  <label class="search">
    <span class="search__icon">
      <img src="@/assets/icons/search.svg" alt="" />
    </span>
    <input
      class="search__input"
      type="text"
      :value="modelValue"
      :placeholder="placeholder"
      @input="$emit('update:modelValue', $event.target.value)"
      ref="input"
    />
  </label>
</template>

<script>
export default {
  name: "BaseInput",
  props: {
    placeholder: String,
    label: String,
    useDebounce: Boolean,
    modelValue: {
      type: [String, Number],
      default: "",
    },
  },
};
</script>

