<template>
  <div class="selection" ref="selection">
    <BaseInput
      v-model="searchFilter"
      use-debounce
      placeholder="Name / email / address"
    />
    <UserList
      v-if="status === 'SUCCESS'"
      :users="getSortedUsers"
      @loadNextPage="nextPage"
    />
    <div v-else-if="status === 'PENDING'">
      <UserItemLoader v-for="item in 5" :key="item" />
    </div>
    <div v-else>Unfortunately, the service is under maintained</div>
  </div>
</template>

<script>
import { getUsers } from "@/api/services/users";
import { debounce, withAsync } from "@/helpers";
import UserList from "@/components/user/UserList.vue";
import BaseInput from "@/components/base/BaseInput.vue";
import UserItemLoader from "@/components/user/UserItemLoader.vue";
import Mark from "mark.js";

const USERS_TO_LOAD = 10;
const MIN_STRING_TO_SEARCH = 1;
const DEBOUNCE_TIME = 500;

export default {
  name: "UserSelection",
  components: {
    UserList,
    BaseInput,
    UserItemLoader,
  },
  data() {
    return {
      searchFilter: "",
      debouncedSearchFilter: "",
      users: "",
      usersCount: 10,
      status: "IDLE",
      marker: null,
    };
  },
  methods: {
    nextPage() {
      this.usersCount = this.usersCount + USERS_TO_LOAD;
    },
    setQueryParam() {
      if (this.$route.params.query) {
        this.searchFilter = this.$route.params.query;
        this.debouncedSearchFilter = this.searchFilter;
      }
    },
    async fetchUsers() {
      this.status = "PENDING";
      const { response, error } = await withAsync(getUsers);
      if (error) {
        this.status = "ERROR";
      } else {
        this.users = response.data;
        this.status = "SUCCESS";
      }
    },
  },
  computed: {
    getSortedUsers() {
      const query = (a) => {
        return (
          a.name
            .toLowerCase()
            .includes(this.debouncedSearchFilter.toLowerCase()) ||
          a.title
            .toLowerCase()
            .includes(this.debouncedSearchFilter.toLowerCase()) ||
          a.address
            .toLowerCase()
            .includes(this.debouncedSearchFilter.toLowerCase()) ||
          a.city
            .toLowerCase()
            .includes(this.debouncedSearchFilter.toLowerCase()) ||
          a.email
            .toLowerCase()
            .includes(this.debouncedSearchFilter.toLowerCase())
        );
      };

      if (this.debouncedSearchFilter.length >= MIN_STRING_TO_SEARCH) {
        return this.users.filter(query).slice(0, this.usersCount);
      }

      return this.users.slice(0, this.usersCount);
    },
  },
  mounted() {
    this.marker = new Mark(this.$refs.selection);
    this.setQueryParam();
    this.fetchUsers();
  },
  watch: {
    searchFilter: debounce(function (newVal) {
      this.debouncedSearchFilter = newVal;
    }, DEBOUNCE_TIME),
  },
  updated() {
    this.marker.unmark();
    if (this.debouncedSearchFilter.length >= MIN_STRING_TO_SEARCH) {
      this.marker.mark(this.debouncedSearchFilter);
    }
  },
};
</script>

<style lang="scss">
.selection {
  padding-right: 12px;
  overflow: auto;
  max-height: 80vh;

  &::-webkit-scrollbar {
    width: 5px;
    height: 5px;
  }

  &::-webkit-scrollbar-track {
    background: transparent;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.16);
    border-radius: 4px;
    border-left: 1.5px solid transparent;
    border-right: 1.5px solid transparent;
  }

  &::-webkit-scrollbar-thumb {
    background: #4d4d4d;
    border-radius: 2px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
}
</style>
